* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

body {
  background-color: rgb(40, 96, 136);
}

.main-content {
  background-color: rgb(255, 235, 0);
}

.container {
  max-width: 750px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 4px solid black;
  padding: 30px;
  border-radius: 5px;
}

.container h1 {
  margin: 0 auto;
  text-align: center;
}

.header {
  border: 2px solid black;
  border-radius: 5px;
  background-color: white;
}

.header h2 {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  font-style: italic;
  font-size: 1em;
}

.superhero-card-border {
  margin-top: 10px;
  margin-bottom: 10px;
  border: 2px solid black;
  border-radius: 5px;
}

.superhero-card {
  background-color: white;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  padding: 5px;
  border: 1px solid black;
}

.small-details {
  height: 3.25em;
}

.small-details h2{
  display: inline;
}

.small-details img {
  float: left;
  margin-right: 1.5em;
}

.full-details img {
  margin: 0 auto;
  display: block;
}

.full-details h2 {
  text-align: center;
}

.sub-title {
  font-style: italic;
  font-size: .75em;
}

.sub-details {
  width: 100%;
}

.sub-details th.sub-heading {
  text-align: center;
  border: 1px solid black;
}

.sub-details th {
  text-align: left;
  border-bottom: 1px solid black;
}

td {
  width: 75%;
  border-bottom: 1px solid black;
}

input {
  border: 1px solid black;
  border-radius: 5px;
  margin-top: 1em;
  width: 100%;
  height: 2.25em;
}